@import "mixins";
@import "settings";

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.main {
	font-family: "Lato", sans-serif;
	font-weight: 400;
	color: #242424;
}
.main a {
	text-decoration: none;
}
.main-svg {
	display: none;
}
.wrapper {
	margin: 0 auto;
	max-width: 1200px;
}
.header {
	padding: 32px 0 14px 21px;
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	flex-wrap: wrap;
}
.btn {
	border: none;
	background-color: transparent;
	color: #fefefe;
	background-color: #0065a4;
	cursor: pointer;
	max-width: 91px;
	font-size: 13px;
	min-height: 33px;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	&--large {
		min-width: 480px;
		min-height: 99px;
		border-radius: 20px;
		line-height: 48px;
		font-size: 25px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.5px;

	}
}
.menu {
	margin-bottom: 41px;
	margin-right: 105px;
	&__link {
	 	text-transform: uppercase;
	 	position: relative;
	 	font-size: 20px;
	 	color: #232323;
	 	letter-spacing: 0.32px;
	 	line-height: 22px;
	}
	&__link:not(:last-child) {
		margin-right: 35px;
	}
	&__link:hover {
		color: #0065a4;
		font-weight: 700;
	}
	&__link:hover:after {
		@include pseudo;
		left: 0;
		right: 0;
		height: 3px;
		background-color: #0065a4;
		bottom: 0px;
		transform: translateY(9px);


	}
}

.main-img {
	width: 100%;
	height: 850px;
	margin-bottom: 79px;
}
.info-about-us {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: 166px;
	color: #242424;
	& img:first-child {
		margin-bottom: 30px;
	}
	& h1 {
		padding-top: 52px;
	}
	&__content {
		border: 4px solid #0065a4;
		max-width: 588px;
		text-align: justify;
		padding: 0 13px;
		font-size: 16px;
		line-height: 30px;
		letter-spacing: 0.32px;
		max-height: 541px;
	}
	&__text:first-child {
		padding-top: 116px;
	}
}
h1 {
	text-align: center;
	font-size: 40px;
	letter-spacing: 0.8px;
	text-transform: uppercase;
	padding-bottom: 26px;
	padding-right: 15px;
}
h2 {
	text-align: center;
	font-size: 30px;
	line-height: 30px;
	text-transform: uppercase;
	letter-spacing: 0.6px;
}
h3 {
	text-align: center;
	font-size: 25px;
	letter-spacing: .5px;
	line-height: 6px;
	text-transform: uppercase;
}
h4 {
	font-size: 18px;
	text-transform: uppercase;
	text-align: center;
}
h5 {
	font-size: 16px;
	color: #312e5c;
}
.bg-logo {
	position: relative;
	min-height: 1069px;
	margin-bottom: 67px;
	background-image: url(../../assets/img/bg-logo.png);
	background-position: 50% 62px;
	background-repeat: no-repeat;
	background-color: #085489;
}
.short-info {
	max-width: 1200px;
	width: 100%;
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	z-index: 3;
	color: #fff;
	font-size: 20px;
	letter-spacing: 0.4px;
	& h2 {
		padding: 68px 0;
	}
	&__b-icons {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		text-align: center;
	}
	&__icon {
		min-height: 380px;
		margin-bottom: 30px;
		padding-top: 26px;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 380px;
		background-color: #6194ca;
		padding: 26px 10px 10px;
	}
	&__svg {
		background-color: #085489;
		width: 158px;
		height: 158px;
		position: relative;
		border-radius: 100%;
		margin-bottom: 35px;
		& svg {
			@include center(both);
			fill: #fff;
		}
	}
	& h3 {
		margin-bottom: 35px;
	}
}
.svg-check {
	width: 96px;
	height: 72px;
}
.svg-tasks {
	width: 66px;
	height: 81px;
}
.svg-time {
	width: 84px;
	height: 80px;
}
.svg-grafic {
	width: 78px;
	height: 77px;
}
.svg-peoples {
	width: 96px;
	height: 82px;
}
.svg-settings {
	width: 96px;
	height: 90px;
}
.news {
	margin-bottom: 195px;
	padding: 0 15px;
	color: #312e5c;
	& h2 {
		margin-bottom: 145px;
	}
	&__content {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__article {
		position: relative;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		justify-content: space-between;
		width: 370px;
		border: 1px solid #e9e8ec;
		padding: 15px;
		& img {
			margin-bottom: 29px;
		}
		& h5 {
			margin-bottom: 10px;
		}
		& p {
			margin-bottom: 27px;
			font-size: 16px;
			color: #312e5c;
			line-height: 28px;
		}
	}
	&__date {
		position: absolute;
		color: #fff;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100px;
		height: 100px;
		border: 5px solid #fff;
		background-color: #0065a4;
		border-radius: 10000px;
		top: 0;
		left: 50%;
		transform: translate(-50%, -50%);
		& time {
			font-size: 13px;
		}
	}
}
.contact-us {
	display: flex;
	flex-direction: column;
	align-items: center;
	& strong {
		font-size: 40px;
		line-height: 55px;
		text-transform: uppercase;
		letter-spacing: 1.3px;
		text-align: center;
		margin-bottom: 113px;
	} 
	& .btn--large {
		margin-bottom: 602px;
	}

}
.bg-img {
	background-image: url(../../assets/img/bg-water-img.jpg);
	-webkit-background-size: cover;
	background-size: cover;
}
footer {
	background-color: #034168;
}
.footer {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	font-family: "Open Sans", sans-serif;
	letter-spacing: 0.28px;
	padding: 59px 14px;
	line-height: 22px;
	font-size: 14px;
	& a {
		display: inline-block;
		color: #fff;
	}
	&__b-links .footer__link {
		color: #fff;
		margin-right: 17px;
		position: relative;
	}
	&__b-links .footer__link:not(:last-child):after {
		content: "|";
		position: absolute;
		display: block;
		right: -12px;
		top: 0;
	}
	
}
.img-before {
	position: relative;
	&:after {
		@include pseudo;
		top: 50%;
		left: -10px;
		transform: translate(-50%, -50%);
		width: 14px;
		height: 13px;
		background-image: url(../../assets/img/small-logo.png);
		-webkit-background-size: cover;
		background-size: cover;
	}	
}
@media (max-width: 1200px) {
	.info-about-us {
		& img {
			width: 100%;

		}
		&__content {
			max-width: 100%;
			max-height: 1000000px;
			margin-bottom: 30px;
			padding-bottom: 50px;
		}
	}
	.bg-logo {
		min-height: 1600px;
		background-position: 50% 50%;

	}
	.short-info {
		&__b-icons {
			justify-content: center;
		}
		&__icon {
			margin: 30px;
		}

	}
	.news {
		&__content {
			flex-direction: column;
			align-items: center;
		}
		&__article {
			margin-bottom: 80px;
			width: 100%;

		}
		& img {
			height: 500px;
		}

	}
}
@media (max-width: 1000px) {
	.header {
		flex-direction: column;
		align-items: center;
		margin: 0;
	}
	.menu {
		
		margin: 50px 0;
	}
	.btn--large {
		min-width: 80%;

	}
}
@media (max-width: 900px) {
	.bg-logo {
		min-height: 3000px;
	}
}
@media (max-width: 700px) {
	.main-img {
		height: 500px;
	}
	.footer {
		justify-content: center;
	}
}
@media (max-width: 550px) {
	.menu {
		display: flex;
		flex-direction: column;
		
		& a {
			margin: 30px 0;
		}
	}
}
@media (max-width: 450px) {
	.main img {
		height: 300px;
	}
	.main-logo img {
		height: 150px;
	}
}